/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

a {
  text-decoration: none;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --red: hsl(0, 100%, 74%);
  --green: hsl(154, 59%, 51%);
  --lightGreen: hsl(154, 65%, 68%);
  --blue: hsl(248, 32%, 49%);
  --darkBlue: hsl(249, 10%, 26%);
  --grayBlue: hsl(246, 25%, 77%);
  --white: hsl(0, 100%, 100%);
  --lightBlack: hsla(0, 0%, 0%, 0.14);
  --lightBlack2: hsla(0, 0%, 0%, 0.09);
  --gray: hsl(0, 0%, 87%);
}

:root {
  --poppins: "Poppins", sans-serif;
}

main {
  min-height: 100vh;
  background-color: var(--red);
  background-image: url(../images/bg-intro-mobile.png);
  background-size: cover;
  display: grid;
  align-content: center;
}
@media (min-width: 48em) {
  main {
    background-image: url(../images/bg-intro-desktop.png);
  }
}

.wrapper {
  max-width: 32.7rem;
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 37.5em) {
  .wrapper {
    max-width: 54rem;
  }
}
@media (min-width: 48em) {
  .wrapper {
    max-width: 72rem;
  }
}
@media (min-width: 64em) {
  .wrapper {
    max-width: 96rem;
  }
}
@media (min-width: 75em) {
  .wrapper {
    max-width: 110rem;
  }
}

body {
  font-size: 1.6rem;
  font-family: var(--poppins);
}

.text {
  text-align: center;
}
@media (min-width: 48em) {
  .text {
    text-align: left;
  }
}

.heading-primary {
  font-size: 2.8rem;
  color: var(--white);
  line-height: 1.3;
}
.heading-primary span {
  display: block;
}
@media (min-width: 37.5em) {
  .heading-primary {
    font-size: 3rem;
  }
}
@media (min-width: 48em) {
  .heading-primary {
    font-size: 3.6rem;
  }
}
@media (min-width: 64em) {
  .heading-primary {
    font-size: 5rem;
  }
}

.description {
  color: var(--white);
  line-height: 1.625;
}

.wrapper {
  display: grid;
  align-content: center;
  row-gap: 6.4rem;
}
@media (min-width: 48em) {
  .wrapper {
    grid-template-columns: 1fr 1.03fr;
    column-gap: 3rem;
  }
}
@media (min-width: 64em) {
  .wrapper {
    column-gap: 4.5rem;
  }
}

.form {
  width: 100%;
  padding: 2.4rem;
  background-color: var(--white);
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 8px 0 var(--lightBlack);
}
@media (min-width: 64em) {
  .form {
    padding: 4rem;
  }
}
.form__content {
  text-align: right;
  margin-bottom: 1.6rem;
}
@media (min-width: 64em) {
  .form__content {
    margin-bottom: 2rem;
  }
}
.form__inner {
  position: relative;
}
.form__input {
  display: block;
  width: 100%;
  height: 5.6rem;
  font-size: 1.4rem;
  font-weight: 600;
  color: var(--darkBlue);
  padding: 1.5rem;
  padding-left: 1.9rem;
  border-radius: 5px;
  border: 1px solid var(--gray);
  background-color: var(--white);
  caret-color: var(--blue);
}
.form__input::placeholder {
  color: var(--grayBlue);
  font-weight: 600;
}
.form__input:focus {
  outline: 0;
  border: 2px solid var(--blue);
}
.form__error {
  position: absolute;
  top: 50%;
  right: 1.6rem;
  transform: translateY(-50%);
  display: none;
}
.form__text {
  font-size: 1.1rem;
  font-style: italic;
  color: var(--red);
  margin-top: 6px;
  display: none;
}
.form__submit {
  width: 100%;
  background-color: var(--green);
  border-radius: 10px;
  margin-bottom: 0.8rem;
  padding: 1.5rem 0;
  text-transform: uppercase;
  color: var(--white);
  font-size: 1.5rem;
  letter-spacing: 1px;
  font-weight: 600;
  border: none;
  box-shadow: 0 -4px 0 var(--lightBlack) inset;
  transition: 0.4s;
  cursor: pointer;
}
.form__submit:hover {
  background-color: var(--lightGreen);
}
.form__disclaime {
  font-size: 1.1rem;
  color: var(--grayBlue);
}
.form__link {
  font-weight: 600;
  color: var(--red);
  line-height: 2;
}

.validate {
  display: block;
}

.text {
  display: flex;
  flex-direction: column;
  row-gap: 1.6rem;
}
@media (min-width: 48em) {
  .text {
    align-self: center;
  }
}

.trial {
  text-align: center;
  margin-bottom: 2.4rem;
  padding: 1.8rem 0;
  width: 100%;
  background-color: var(--blue);
  border-radius: 10px;
  box-shadow: 0 8px 0 var(--lightBlack);
}
.trial__text {
  font-size: 1.5rem;
  color: var(--white);
  line-height: 1.73;
}
.trial__text span {
  display: block;
}
@media (min-width: 48em) {
  .trial__text span {
    display: inline-block;
  }
}